import {Container} from '../../../common/style/Container';
import {Arrow} from './assets';
import {devData} from '../data';
import {CurrentDev} from './currentDev/CurrentDev';
import {ButtonArrow, CurrentDevWrapper, SubTitle} from './currentDev/style';
import {
    AboutText,
    CardImageMobile,
    Cards,
    CardsMobile,
    CurrentDevMobile,
    DescriptionWebBlock,
    DescriptionWebContent,
    DescriptionWebWrapper,
    SubTitleMobile,
    Title,
    TitleMobile
} from './style';


export const DescriptionWeb = () => {

    return (
        <DescriptionWebWrapper>
            <Container>
                <DescriptionWebContent>
                    <DescriptionWebBlock>
                        <Title>
                            <span>WEB-</span> <br/>  
                        </Title>
                        <AboutText>
                               <span>VEON-TECH</span>   
                              
                            -: ,  SEO ,  ,
                              
                             ,    ,  ,   
                            CRM, 
                             ,  -,    ,  
                            .
                        </AboutText>
                        <AboutText>
                            Digital  <span>VEON-TECH</span>     
                            performance marketing.
                                     .  
                               
                             .     ,   ,
                              
                              .
                        </AboutText>
                    </DescriptionWebBlock>
                    <Cards>
                        {devData.map((el, index) => <CurrentDev key={index} card={el}/>)}
                    </Cards>
                    <CardsMobile>
                        <CurrentDevWrapper>
                            <CardImageMobile>
                                {devData.map((el, index) => <img key={index} src={el.img} alt="img"/>)}
                            </CardImageMobile>
                            <TitleMobile> </TitleMobile>
                            <SubTitle>    -,   -,  </SubTitle>
                            <ButtonArrow>
                                 
                                <Arrow/>
                            </ButtonArrow>
                        </CurrentDevWrapper>
                    </CardsMobile>

                </DescriptionWebContent>
            </Container>
        </DescriptionWebWrapper>
    );
};
