import {Swiper, SwiperSlide} from "swiper/react";

import "swiper/css";
import "swiper/css/pagination";

import {SwiperCompanyWrapper} from "./style";
import {Autoplay} from "swiper";
import {clients} from "../../../pages/home/company/data";
export const SliderCompany = () => {
    return (
        <SwiperCompanyWrapper>
            <Swiper
                autoplay={true}
                speed={200}
                loop={true}
                slidesPerView={6}
                spaceBetween={30}
                modules={[Autoplay]}
                breakpoints={{
                    320: {

                        slidesPerView: 1.8,
                    },
                    500: {

                        slidesPerView: 3.5,
                    },

                    1000: {

                        slidesPerView: 4.5,
                    },

                    1600: {

                        slidesPerView: 6.5,
                    },


                }}

            >
                {clients.map((el, index) => (
                    <SwiperSlide key={index}>
                        <img src={el.img} alt='img'/>
                    </SwiperSlide>
                ))}
            </Swiper>
        </SwiperCompanyWrapper>
    );
};
