import styled from 'styled-components';


export const SwiperReviewsWrapper = styled.div`
  max-width: 1120px;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 15px;
background: #E9FCE5;


.swiper {
    width: 100%;
    height: 100%;
    .swiper-wrapper {
        margin-bottom: 20px;
    }
  }
  
  .swiper-slide {
    align-items: center;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;
    
    color: #000000;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .swiper-pagination {
    position: relative;
  }
  .swiper-pagination-bullet-active {
    position: relative;
    border: 4px solid #6EEC4E;
    
  }

  
`

export const Review = styled.p`
width: 100%;

@media (max-width: 768px) {
font-weight: 600;
font-size: 16px;
line-height: 20px;

color: #000000;

}

`
