import React from 'react';
import {
    Background,
    ContactsForm,
    ContactsBlock,
    ContactsWrapper,
    ImgBg,
    Input,
    Title,
    Textarea,
    ButtonForm, SubText, SubTitle
} from "./style";
import Bg from './assets/bg.png'

export const Contacts = () => {
    return (
        <ContactsWrapper>
            <ContactsBlock>
                <Title> ? - </Title>
                <SubTitle>   <span></span>  !</SubTitle>
                <ContactsForm>
                    <Input type="text" placeholder=''/>
                    <Input type="email" placeholder='E-mail'/>
                    <Input type="phone" placeholder=''/>
                    <Textarea placeholder=''/>
                    <SubText>
                           ,         
                             <a href="#">   </a>
                    </SubText>
                    <ButtonForm type={"button"}></ButtonForm>
                </ContactsForm>
            </ContactsBlock>
            <Background>
                <ImgBg src={Bg} alt="Bg"/>
            </Background>
        </ContactsWrapper>
    );
};

