import styled from "styled-components";

export const CompanyWrapper = styled.div`
background: #E9FCE5;
padding: 50px 0;
@media (max-width: 768px) {
padding: 25px 0;
}

`
export const Title = styled.h3`
font-weight: var(--fw-medium);
font-size: 17px;
line-height: 21px;
color: #000000;
margin-bottom:10px;
@media (max-width: 768px) {
    text-align: center;
    font-size: 25px;
    line-height: 31px;
    font-weight: var(--fw-extra-bold);
    margin-bottom: 60px;
    margin-top: -80px;
    color: #FFFFFF;
  }

`

